* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body, header, footer {
    background: linear-gradient(45deg, #2b2b2b, #4e2a6e, #1a1a1a);
    color: #ffffff;
    font-family: system-ui, -apple-system, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header {
    padding: 1rem;
    text-align: center;
    font-size: 25px;
    font-weight: bold;
}

footer {
    padding: 1rem;
    text-align: center;
    color: #ffffff;
    font-size: 1rem;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
  }
  
  .nav-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .nav-logo {
    width: 100px;
    height: auto;
  }
  
  .search-bar {
    flex: 1;
    display: flex;
    justify-content: center;
  }
  
  
  .search-input {
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .search-button {
    background-color: #f90;
    color: #fff;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .search-button:hover {
    background-color: #e80;
  }
  
  .nav-links {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  
  .nav-links a {
    color: #fff;
    text-decoration: none;
  }
  
  .nav-links a:hover {
    text-decoration: underline;
  }
  
  .menu, .close {
    display: none; /* Cho responsive nếu cần */
  }

.nav-links {
    display: flex;
    gap: 2rem;
}

.nav-links svg {
    display: none;
}

.nav-links a {
    color: #ffffff;
    text-decoration: none;
    font-size: 0.875rem;
    font-weight: 500;
    transition: color 0.3s;
}

.nav-links a:hover {
    color: #9333ea;
}

.hero {
    position: relative;
    height: 600px;
    overflow: hidden;
    margin-top: 64px;
}

.hero svg {
    display: none;
}

.hero-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.hero-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    transition: opacity 1s ease-in-out;
}

.hero-image.active {
    opacity: 1;
}

.hero::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.8));
    z-index: 1;
}

.hero button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    cursor: pointer;
    z-index: 2;
}

.prev-button {
    left: 20px;
}

.next-button {
    right: 20px;
}

.hero button:hover {
    background-color: rgba(255, 255, 255, 0.3);
}

.section {
    max-width: 1200px;
    margin: 0 auto;
    padding: 4rem 1rem;
}

.section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
}

.section-title {
    font-size: 2rem;
    font-weight: bold;
}

.games-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
}

.game-card {
    position: relative;
    width: 190px;
    height: 254px;
    background-color: #000;
    display: flex;
    flex-direction: column;
    justify-content: end;
    padding: 12px;
    gap: 12px;
    border-radius: 8px;
    cursor: pointer;
    color: white;
    overflow: hidden;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out, border-radius 0.3s ease-in-out;
    background: linear-gradient(-45deg, #f17cf3 0%, #66e0fb 100% );
}

.game-card::before {
    content: '';
    position: absolute;
    inset: 0;
    left: -5px;
    margin: auto;
    width: 200px;
    height: 264px;
    border-radius: 10px;
    background: linear-gradient(-45deg, #f17cf3 0%, #66e0fb 100% );
    z-index: -10;
    pointer-events: none;
    transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.game-card::after {
    content: "";
    z-index: -1;
    position: absolute;
    inset: 0;
    /* background: linear-gradient(-45deg, #fc00ff 0%, #00dbde 100%); */
    transform: translate3d(0, 0, 0) scale(0.95);
    filter: blur(20px);
}

.game-card:hover {
    transform: scale(0.98);
    box-shadow: 0px 0px 30px 1px rgba(0, 255, 117, 0.3);
    border-radius: 20px;
}

.game-card:hover::after {
    filter: blur(30px);
}

.game-card:hover::before {
    transform: rotate(-90deg) scaleX(1.34) scaleY(0.77);
}

.game-card:active {
    transform: scale(0.95);
}

.game-title {
    font-size: 20px;
    font-weight: 700;
    text-transform: capitalize;
    color: #ffffff;
}

.search-container {
    display: flex;
    align-items: center;
}

.search-input {
    padding: 0.5rem;
    border: none;
    border-radius: 4px 0 0 4px;
    background-color: rgba(255, 255, 255, 0.1);
    color: #ffffff;
    font-size: 0.875rem;
}

.search-input::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.search-button {
    padding: 0.5rem;
    border: none;
    border-radius: 0 4px 4px 0;
    background-color: #9333ea;
    color: #ffffff;
    cursor: pointer;
    transition: background-color 0.3s;
}

.search-button:hover {
    background-color: #7928ca;
}

.hidden {
    display: none !important;
}

.menu {
    display: none;
}

@media (max-width: 768px) {
    .menu {
        display: block;
        cursor: pointer;
        position: absolute;
        right: 20px;
        top: 70px;
        z-index: 1001;
        width: 36px;
        height: 36px;
    }

    .nav-links {
        display: none;
        background: #000000;
        flex-direction: column;
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        padding: 2rem;
        z-index: 1000;
        transform: translateX(100%);
        transition: transform 0.3s ease-in-out, background 0.3s ease-in-out;
    }

    .nav-links.active {
        display: flex;
        transform: translateX(0);
    }

    .hero {
        height: 400px;
    }

    .games-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }
}

.hover\:bg-purple-700:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(126 34 206 / var(--tw-bg-opacity, 1));
}

.hover\:text-purple-400:hover {
  --tw-text-opacity: 1;
  color: rgb(192 132 252 / var(--tw-text-opacity, 1));
}

.hover\:shadow-2xl:hover {
  --tw-shadow: 0 25px 50px -12px rgb(0 0 0 / 0.25);
  --tw-shadow-colored: 0 25px 50px -12px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

@media (min-width: 640px) {

  .sm\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .sm\:px-6 {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media (min-width: 768px) {

  .md\:mb-0 {
    margin-bottom: 0px;
  }

  .md\:flex-row {
    flex-direction: row;
  }
}

@media (min-width: 1024px) {

  .lg\:grid-cols-4 {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }

  .lg\:px-8 {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
